import { Button, Grid, IconButton, Paper, Table, TableBody, TableCell, TableContainer, TableRow, TextField } from "@mui/material";
import DeleteIcon from '@mui/icons-material/Delete';
import { useForm } from "react-hook-form";
import { db } from '../../../Services/Storage';
import { useLiveQuery } from "dexie-react-hooks";

export default function MemCardsAdd() {
    const {
        register,
        handleSubmit,
        formState: { submitCount },
        reset,
    } = useForm();

    const cards = useLiveQuery(async () => {
        return await db.cards.reverse().toArray();
    }, []);

    async function addCard({phrase, meaning, example}) {
        const id = await db.cards.add({phrase, meaning, example, dateAdded: Date.now()});
        reset()
    }

    return (
        <Grid container spacing={6} sx={{p: 8}} justifyContent={'center'}>
            <Grid item xs={12} md={3}>
            </Grid>
            <Grid item xs={12} md={6}>
                <form onSubmit={handleSubmit(addCard)} key={submitCount}>
                    <Paper sx={{p: 5}}>
                        <Grid container spacing={2} sx={{flexDirection: 'column'}} justifyContent={'space-between'} minHeight={'33vh'}>
                            <Grid item xs={12}>
                                <TextField {...register('phrase')} margin="normal" label="Phrase" variant="standard" fullWidth />
                                <TextField {...register('meaning')} margin="normal" label="Meaning" variant="standard" fullWidth multiline />
                                <TextField {...register('example')} margin="normal" label="Example" variant="standard" fullWidth multiline />
                            </Grid>
                            <Grid item xs={12}>
                                <Button variant="contained" fullWidth type="submit">Add</Button>
                            </Grid>
                        </Grid>
                    </Paper>
                </form>
            </Grid>
            <Grid item xs={12} md={3}>
            </Grid>

            <Grid item xs={12} md={12}>
                <TableContainer component={Paper} sx={{maxHeight: '500px' }}>
                    <Table sx={{ minWidth: 650}} aria-label="simple table">
                        <TableBody>
                            {
                                cards?.map(card => (
                                    <TableRow key={card.id}>
                                        <TableCell component="th" scope="row"><h2>{card.phrase}</h2></TableCell>
                                        <TableCell>{card.meaning}</TableCell>
                                        <TableCell>{card.example}</TableCell>
                                        <TableCell>{(new Date(card.dateAdded)).toString()}</TableCell>
                                        <TableCell>
                                            <IconButton onClick={() => db.cards.delete(card.id)}>
                                                <DeleteIcon />
                                            </IconButton>
                                        </TableCell>
                                    </TableRow>
                                ))
                            }
                        </TableBody>
                    </Table>
                </TableContainer>
            </Grid>
        </Grid>
    )
}